.App {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 70%;
  margin: auto;
  text-align: start;
  padding: 50px 200px;
}

.navbar {
  font-size: large;
  text-align: center;
  margin: auto;
  padding: 10px;
  height: 100px;
}

.navbar .navbar-nav {
  display: inline-block;
  height: 100px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.navbar img {
  display: inline-block;
  padding-right: 100px;
  width: 100px;
  vertical-align: middle;
}

.navbar .nav-link {
  width: 250px;
  margin: 0 50px;
  text-decoration: none;
  text-transform: uppercase;
  font-family: sans-serif;
  color: #222222;
  font-weight: bolder;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 50px;
}

.main-table {
  width: 90%;
  padding: 50px;
}

.react-bootstrap-table {
  margin: auto;
  margin-bottom: 100px;
}

thead .order {
  margin-right: 10px;
}

thead .react-bootstrap-table-sort-order {
  margin-right: 10px;
}

thead tr {
  height: 60px;
  vertical-align: top;
}

td {
  border-bottom: #cccccc solid 1px !important;
  border-right: #eeeeee solid 1px !important;
}

th {
  padding: 0 50px 0 5px;
  text-align: left;
  border-bottom: #cccccc solid 3px !important;
}

td {
  padding: 5px 30px 10px 5px;
  text-align: left;
  min-width: 150px;
  vertical-align: top;
}

.sr-only {
  display: none;
}

.request {
  width: 60%;
}

.navbar-nav {
  max-width: 90vw;
}

.email-page tr {
  height: 35px;
}

.email-page table {
  min-width: 883px;
}

.email-page th {
  padding: 0 50px 0 0;
}

.email-table {
  margin-bottom: 50px;
}

@media (max-width: 1750px) {
  .request {
    width: 70%;
  }
}

@media (max-width: 1550px) {
  .App {
    padding: 50px 100px;
  }

  .main-table {
    width: 90%;
  }
}

@media (max-width: 1150px) {
  .request {
    width: 80%;
  }

  .App {
    padding: 50px 50px;
  }

  .nav-link {
    display: inline-block;
    margin: 0 !important;
  }

  .navbar-nav {
    width: 250px;
    vertical-align: top;
    height: auto !important;
  }

  .App {
    padding-top: 50px;
  }
}

@media (max-width: 970px) {
  .navbar img {
    padding-right: 50px;
    padding-left: 50px;
  }

  .App {
    width: 90%;
  }
}

@media (max-width: 750px) {
  .email-page tr {
    height: 70px;
  }

  .email-page table {
    width: 483px;
    min-width: 483px;
  }

  .email-page th {
    padding: 0;
  }

  .email-table {
    height: 180px;
  }
}

@media (max-width: 502px) {
  .request {
    width: 90%;
  }

  .App {
    padding-top: 100px;
  }
}

@media (max-width: 450px) {
  .App {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 120px;
  }
}
